<template>
	<div class="block iconfont" @click='userCheck' @contextmenu.prevent='mark()' :class='{"icon-zhadan":item.status==1 && item.isOpen,zero:item.count==0,"icon-1218biaoqianhongqi":item.isMarked}'>
		<!-- <span v-if='item.isOpen && item.count!=0'>{{item.count}}</span> -->
        <span v-if='item.isOpen && item.count!=0'>{{item.count}}</span>
	</div>
</template>
<script>
export default {
	data(){
		return {
			item:this.blockValue
		}
	},
	props:['blockValue'],
	methods:{
		userCheck(){
            if(!this.item.isMarked){
                this.item.isOpen = true;
                if(this.item.status==1){    //如果有雷 game over
                    this.$emit('gameover')
                }else{
                    this.$emit('countPoint',this.item.position)  //无雷 传递定位点
                }
            }
        },
        mark(){
            if(!this.item.isOpen){
                this.item.isMarked = !this.item.isMarked
                this.$emit('marked')  //传递定位点
            }
        }
	}
}
</script>
<style scoped>
	.block{
		width: 30px;height: 30px;background:#333;border: 1px solid #fff;color: #fff;vertical-align: middle;line-height: 30px;cursor: pointer;
    }
    .icon-1218biaoqianhongqi{color: #e16531;}
	.icon-zhadan{color: red}
    .zero{background: #999;}


</style>



