<template>
    <div class="index">
        <p>{{status==0?"游戏中":"游戏结束"}}</p>
        <p>设置区域大小：<Input v-model="setVal1" class="box"></Input> x <Input v-model="setVal2" class="box"></Input><Button type="primary" class="checkInit" @click="setInit">开始</Button></p>
        <div class="list">
            <template v-for='(item,x) in blockList'>
                <div :key="x">
                    <block v-for='(value,y) in item' :key="y" :blockValue='value' @gameover='gameover' @countPoint='countPoint' @marked='marked'></block>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
import block from "./block";
import * as _ from "lodash";
export default {
  name: "HelloWorld",
  data() {
    return {
      setVal1: localStorage.setVal1,
      setVal2: localStorage.setVal2,
      blockList: [],
      boomList: [],
      status: 0
    };
  },
  mounted() {
    this.init(this.setVal1, this.setVal2);
  },
  methods: {
    setInit() {
      this.blockList = [];
      setTimeout(() => this.init(this.setVal1, this.setVal2), 0);

      localStorage.setItem("setVal1", this.setVal1);
      localStorage.setItem("setVal2", this.setVal2);
    },
    init(x, y) {
      //x -- 行数   y--列数
      let min = x > y ? y : x;
      let sum = x * y;
      let randomArr = []; //有雷列表
      let has = {};
      for (let i = 0; i < parseInt(sum/5); i++) {
        let pushRandom = [_.random(x - 1), _.random(y - 1)];
        if (!_.has(has, pushRandom.toString())) {
          randomArr.push(pushRandom);
          has[randomArr[i]] = true;
        }
      }
      let myarr = new Array(); //先声明一维
      const blockCommon = { isOpen: false, isMarked: false };
      for (let i = 0; i < min; i++) {
        //一维长度为x
        myarr[i] = new Array(); //再声明二维
        for (let j = 0; j < y; j++) {
          //二维长度为j   //0表示无雷，1表示有雷
          myarr[i][j] = _.assign({ status: 0, position: [i, j] }, blockCommon); // 赋值，每个数组元素的值为i+j
          for (var k = 0; k < randomArr.length; k++) {
            if (_.isEqual(randomArr[k], [i, j])) {
              myarr[i][j] = _.assign(
                { status: 1, position: [i, j] },
                blockCommon
              );
            }
          }
        }
      }
      this.status = 0;
      this.boomList = randomArr;
      this.blockList = _.cloneDeep(myarr);
    },
    gameover() {
      this.status = 1;
      this.$Modal.confirm({
        title: "游戏结束",
        content: "<p>你踩到雷了！是否重新开始</p>",
        onOk: () => {
          let initVal1 = _.isEmpty(localStorage.setVal1)
            ? 9
            : localStorage.setVal1;
          let initVal2 = _.isEmpty(localStorage.setVal2)
            ? 9
            : localStorage.setVal2;
          this.blockList = [];
          setTimeout(() => this.init(initVal1, initVal2), 0);
        },
        onCancel: () => {
          this.$Message.info("那就降低难度 再开始吧~~");
        }
      });
    },
    countPoint(position) {

      let countNumber = 0;
      //找到四周的位置
      const find = n => {
        let x = Math.abs(n.position[0] - position[0]);
        let y = Math.abs(n.position[1] - position[1]);
        if (x >= 0 && x < 2 && y >= 0 && y < 2) {
          countNumber += n.status;
        }
      };

      _.map(_.flatten(this.blockList), find);
      this.blockList[position[0]][position[1]]["count"] = countNumber;
      //如果周围没有雷
      var setOtherVal1 = position[0]+1>=this.setVal1 ?this.setVal1-1:position[0]+1
      var setOtherVal3 = position[0]-1<0 ? 0 : position[0]-1
      var setOtherVal2 = position[1]+1>=this.setVal1 ?this.setVal1-1:position[1]+1
      var setOtherVal4 = position[1]-1<0 ? 0 : position[1]-1


        if(countNumber==0 && (!this.blockList[position[0]][setOtherVal4]["isOpen"] || !this.blockList[setOtherVal3][position[1]]["isOpen"] || !this.blockList[setOtherVal1][position[1]]["isOpen"] || !this.blockList[position[0]][setOtherVal2]["isOpen"])){
            this.blockList[setOtherVal1][position[1]]["isOpen"] = true
            this.blockList[setOtherVal3][position[1]]["isOpen"] = true
            this.blockList[position[0]][setOtherVal2]["isOpen"] = true
            this.blockList[position[0]][setOtherVal4]["isOpen"] = true
            this.countPoint([setOtherVal1,position[1]])
            this.countPoint([setOtherVal3,position[1]])
            this.countPoint([position[0],setOtherVal2])
            this.countPoint([position[0],setOtherVal4])
        }
    },
    marked(){
        if(this.isWin){
            this.status=1;
            this.$Message.success({
                top:50,
                content:'你赢了 好厉害！'
            });
        }
    }
  },
  computed:{
      isWin(){
          let isWin = true;
          _.map(this.blockList,(value)=>{
              _.map(value,(block)=>{
                  if(block.status==1){
                      if(!block.isMarked){
                        isWin = false;
                      }
                  }
              })
          })
          return isWin
      }
  },
  components: {
    block
  }
};
</script>
<style>
.index {
  width: fit-content;
  margin: 0 auto;
}
.index p {
  margin-bottom: 20px;
}
.list {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  width: fit-content;
  margin: 0 auto;
}
.checkInit {
  margin-left: 10px;
}
.box {
  width: 30px;
}
.box input {
  text-align: center;
}
</style>



